"use client";
import { motion } from "framer-motion";
import { ReactNode } from "react";

interface DialogBaseProps {
  title: string;
  onClose: () => void;
  children: ReactNode;
}

export default function DialogBase({ title, onClose, children }: DialogBaseProps) {
  return (
    <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
      <motion.div
        initial={{ scale: 0.8, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        exit={{ scale: 0.8, opacity: 0 }}
        className="bg-white rounded-3xl shadow-2xl p-6 w-[90%] max-w-md"
      >
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-indigo-700">{title}</h2>
          <button onClick={onClose} className="text-gray-500 hover:text-gray-800 text-lg">
            ✕
          </button>
        </div>
        {children}
      </motion.div>
    </div>
  );
}
