"use client";
import DialogBase from "./DialogBase";

interface HelpDialogProps {
  open: boolean;
  onClose: () => void;
}

export default function HelpDialog({ open, onClose }: HelpDialogProps) {
  if (!open) return null;

  return (
    <DialogBase title="עזרה / הוראות" onClose={onClose}>
      <p className="text-gray-700 leading-relaxed">
        🎮 ברוכה הבאה לפלטפורמת המשחקים ללימוד אנגלית!  
        <br />בחרי משחק מהרשימה ותתחילי לתרגל בצורה חווייתית ומעניינת.
        <br />
        כל משחק מחזק תחום אחר: דקדוק, אוצר מילים או חריזה.  
        <br />בהצלחה! 🚀
      </p>
    </DialogBase>
  );
}
